import React ,{useState,useEffect} from 'react'
import { toast } from 'react-toastify'

import 'react-toastify/dist/ReactToastify.css'
import './form.css'
toast.configure()
function Tneb_cal() {
    const [addrtype, setAddrType] = useState({Tariff_Check:"",
   
    Consumed_Units:""
                     })
                     const [sum,setTariff] = useState("0")
        useEffect(()=>{
              let tar=0,money=0;
              if(addrtype.Tariff_Check ==="1")
              {
                  money=1.5;
              } 
              else if(addrtype.Tariff_Check ==="2")
              {
                money=2.0;
              }
              else if(addrtype.Tariff_Check ==="3")
              {
                money=2.5;
              }
              else if(addrtype.Tariff_Check ==="4")
              {
                money=0.5;
              }

              
              if(addrtype.Consumed_Units<=100)
              {
                setTariff('0')
              }
              else
              {
                      tar=addrtype.Consumed_Units-100;
                      setTariff(tar*money)
              }
               console.log(sum)
        },[addrtype])
           function result(e){
               e.preventDefault();
               toast.success('AMOUNT:'+sum, {
                position: "top-center",
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                });
               
           }
           function reset(){
                   setAddrType({Consumed_Units:""});
           }  
         return(
              <form>
                  <h1>EB CALCULATOR</h1>
                  <h3 className="my-5">100 units free for all</h3>
                 <label forhtml="exampleInputEmail1" class="form-label my-2">Choose of unit</label>
                    <select class="form-select form-select-lg mb-3 my-3"
                   
                    onChange={e =>{setAddrType({...addrtype,Tariff_Check:e.target.value})}}>
                    <option selected >Open this select menu</option> 
                    <option value="1">HOUSE</option>
                    <option value="2">DOMESTIC</option>
                    <option value="3">POWER LOOM</option>
                    <option value="4">AGRICULTURE</option>

                    </select>
                    
                <label forhtml="exampleInputEmail1" class="form-label my-2">Enter Consumed units</label>
              <input type="number" className="form-control my-3" id="exampleInputEmail1"
          value={addrtype.Consumed_Units} 
         onChange={e =>{setAddrType({...addrtype,Consumed_Units:e.target.value})}} />
             
             <button type="button" class="btn btn-light" onClick={result}>RESULT</button>
             <button type="button" class="btn btn-light mx-2" onClick={reset}>RESET</button>
              
             </form>
         )
}
export default Tneb_cal
